export default function HardwarePage() {
  const hardware = [
    {
      category: 'Compute',
      items: [
        { name: 'Raspberry Pi 4 Model B (8GB)', quantity: 4, purpose: 'Cluster nodes (1 master, 3 workers)' },
      ]
    },
    {
      category: 'Storage',
      items: [
        { name: 'Samsung EVO 64GB MicroSD Cards', quantity: 4, purpose: 'Boot drives' },
        { name: 'Samsung T5 500GB SSD', quantity: 1, purpose: 'Shared storage (optional)' },
      ]
    },
    {
      category: 'Networking',
      items: [
        { name: 'TP-Link 8-Port Gigabit Switch', quantity: 1, purpose: 'Network connectivity' },
        { name: 'Cat6 Ethernet Cables (0.5ft)', quantity: 4, purpose: 'Node connections' },
      ]
    },
    {
      category: 'Power',
      items: [
        { name: 'Anker 60W 6-Port USB Charger', quantity: 1, purpose: 'Power all nodes' },
        { name: 'USB-C Power Cables', quantity: 4, purpose: 'Power delivery' },
      ]
    },
    {
      category: 'Housing',
      items: [
        { name: 'Raspberry Pi Cluster Case', quantity: 1, purpose: 'Physical organization' },
        { name: 'Cooling Fans (30mm)', quantity: 4, purpose: 'Temperature management' },
      ]
    },
  ]

  return (
    <div className="max-w-4xl mx-auto px-8 py-12">
      <h1 className="text-4xl font-bold mb-4 dark:text-white">Hardware</h1>
      <p className="text-xl text-gray-600 dark:text-gray-300 mb-8">
        Complete list of components used in the cluster build.
      </p>

      <div className="space-y-8">
        {hardware.map((category, idx) => (
          <div key={idx} className="border border-gray-200 dark:border-gray-700 rounded-lg p-6">
            <h2 className="text-2xl font-semibold mb-4 dark:text-white">{category.category}</h2>
            <div className="space-y-4">
              {category.items.map((item, itemIdx) => (
                <div key={itemIdx} className="flex justify-between items-start border-b border-gray-100 dark:border-gray-800 pb-3 last:border-0">
                  <div className="flex-1">
                    <h3 className="font-medium dark:text-white">{item.name}</h3>
                    <p className="text-sm text-gray-600 dark:text-gray-400">{item.purpose}</p>
                  </div>
                  <div className="ml-4 text-right">
                    <span className="inline-block px-3 py-1 bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200 rounded-full text-sm">
                      x{item.quantity}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>

      <div className="mt-8 p-6 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
        <h3 className="font-semibold mb-2 dark:text-white">Total Cost Estimate</h3>
        <p className="text-gray-700 dark:text-gray-300">
          Approximately $400-500 USD depending on sales and component choices.
        </p>
      </div>
    </div>
  )
}
